import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'mat-star-rating',
  templateUrl: './star-rating.component.html',
  styleUrls: ['./star-rating.component.scss'],
  encapsulation: ViewEncapsulation.Emulated
})
export class StarRatingComponent implements OnInit {

  @Input('rating') private rating: number;
  @Input('starCount') public starCount: number;
  @Input('color') color: string;
  @Output() private ratingUpdated = new EventEmitter();

  private snackBarDuration = 2000;
  public ratingArr = [];

  constructor(private snackBar: MatSnackBar) {
  }


  ngOnInit() {
    for (let index = 0; index < this.starCount; index++) {
      this.ratingArr.push(index);
    }
  }
  onClick(rating: number) {
    console.log(rating);
    this.ratingUpdated.emit(rating);
    return false;
  }

  showRatingLabel(level: number){
    let result = '';
    switch (level) {
      case 1: result = 'Poor'; break;
      case 2: result = 'Not Good'; break;
      case 3: result = 'Fine'; break;
      case 4: result = 'Really Good'; break;
      case 5: result = 'Excellent'; break;
    }
    return result;
  }

  showIcon(index: number) {
    if (this.rating >= index + 1) {
      return 'star';
    } else {
      return 'star_border';
    }
  }

}
export enum StarRatingColor {
  primary = 'primary',
  accent = 'accent',
  warn = 'warn'
}
