import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {UUID} from 'angular2-uuid';
import { MatSnackBar } from '@angular/material/snack-bar';
import {StreamActivity, StreamClientService} from '@brightpath/activity-stream';

@Component({
  selector: 'provider-private-message',
  templateUrl: './provider-private-message.component.html',
  styleUrls: ['./provider-private-message.component.scss']
})
export class ProviderPrivateMessageComponent implements OnInit {

  @Input() customerId;
  @Input() providerId;
  @Output() close = new EventEmitter<boolean>();

  constructor(private streamClientService: StreamClientService, private snackbar: MatSnackBar) {
  }

  onPrivateMessage(privateMessage: StreamActivity) {
    privateMessage.actor = 'customers:' + this.customerId;
    privateMessage.threadId = UUID.UUID();
    privateMessage.to = [`notification:${this.providerId}`];
    privateMessage.target = `inbox:${this.providerId}`;
    this.streamClientService.getToken('inbox', this.providerId).then((response: any) => {
      const token = response.token;
      this.streamClientService.addActivity(privateMessage, 'inbox', this.providerId, token).then((privateMessageResponse) => {
        this.snackbar.open('Sent Private Message', 'OK', {
          verticalPosition: 'top',
          duration        : 2000
        });
      });
      this.close.emit(true);
    });
  }

  ngOnInit() {
  }

}
