import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import { MatSnackBar } from '@angular/material/snack-bar';
import {ProvidersService} from '@brightpath/scheduling';

@Component({
  selector: 'provider-rating',
  templateUrl: './provider-rating.component.html',
  styleUrls: ['./provider-rating.component.scss']
})
export class ProviderRatingComponent implements OnInit {

  providerId = '';
  showWidget = false;

  constructor(private providerService: ProvidersService,
              private http: HttpClient,
              private snackbar: MatSnackBar,
              private router: Router,
              private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe((params) => {
      console.log(params);
      if (params.id) {
        this.providerId = params.id;
      }
      this.showWidget = true;
    });

  }
}
